.journal__main-content {
  display: flex;
  flex-direction: column-reverse;

  @media (min-width: 768px) {
    flex-direction: row;
  }
}

.journal__sidebar {
  background-color: $dark-grey;
  color: white;
  display: flex;
  flex-direction: column;

  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  @media (min-width: 768px) {
    width: 450px;
    height: 100vh;
  }
}

.journal__sidebar-navbar {
  display: flex;
  justify-content: space-between;
}

.journal__sidebar-navbar h3 {
  font-weight: lighter;
}

.journal__new-entry {
  align-items: center;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 30px;
  width: 100%;

  transition: color 0.3s ease;

  &:hover {
    color: darken($color: white, $amount: 20);
  }
}

.journal__entries {
  flex: 1 1 auto;
  margin-top: 30px;
  overflow-y: scroll;
  
}

.journal__entry {
  background-color: white;
  border-radius: 4px;
  color: $dark-grey;
  display: flex;
 flex-direction: column;
 align-items: center;
   margin-bottom: 10px;
   margin-right: 20px;
  overflow: hidden;
  @media (min-width: 550px) {
    justify-content: space-between;
    flex-direction: row;
  }
}

.journal__entry-picture {
  width: 75px;
    height: 75px;
  margin: 5px;

 
}

.journal__entry-body {
  padding: 10px;
}

.journal__entry-title {
  font-size: 14px;
  font-weight: bold;
}

.journal__entry-content {
  font-size: 10px;
}

.journal__entry-date-box {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px;
  font-size: 12px;
}
